@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.bodyDiv {
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #070a0d, #121921, #0f0613);
    background-size: cover;
    font-family: 'Raleway', sans-serif;
}

.profileContent{
    height: 96vh;
    display: flex;
	flex-direction: column;
	justify-content: space-around;
}

.profileTop{
    display: flex;
	flex-direction: column;
	align-items: center;

    height: 30vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}


.userProfile {
    display: flex;
	flex-direction: column;
	align-items: center;
    margin-top: 22vh;
}

.userProfile img {
    height: 19vh;
    border: 20px solid #292929;
    border-radius: 50%;
}

.profileTop span {
    color: white;
    font-size: 2em;
}

.profileBottom {
    margin-top: 20vh;
	display: flex;
	justify-content: space-around;
	align-content: center;
}

.profileLeft span {
    color: #6000FC;
    font-size: 1.7em;
}

.profileLeft .list-group {
    height: 50vh;
    width: 400px;
    overflow-y: auto;
    background-color: rgba(200, 200, 200, 0.05) !important;
    font-size: 1.4em;
    border: 3px solid #6000FC;
    border-collapse: separate;
    border-radius: 7px;
}

.profileMiddle {
    width: 400px;
    display: flex;
	flex-direction: column;
	align-items: center;
}

.profileData {
    display: flex;
	flex-direction: column;
	align-items: center;
    margin-bottom: 15px;
    color: white;
    font-size: 1.25em;
}

.dataTitle {
    color: #00F0FF;
    font-size: 1.5em;
}

.profileRight .list-group{
    width: 400px;
    height: 50vh;
    overflow-y: auto;
    background-color: rgba(200, 200, 200, 0.05) !important;
    border: 3px solid #6000FC;
    border-collapse: separate;
    border-radius: 7px;
}

.profileRight {
    font-size: 1.1em;
}

.profileRight span {
    color: white;
}

.profileRightTitle {
    color: #6000FC !important;
    font-size: 1.4em;
}
.bodyDiv {
    margin: 0;
    padding: 0;
    background-color: #0B0A0A;
    font-family: 'Raleway', sans-serif;
    width: 100vw;
    min-height: 100vh;
}

.elipseUp{
    float:left;
    width:12%;
}

.homeContent{
    padding-top: 2%;
	display: flex;
	justify-content: space-around;
	align-items: stretch;
}

.leftSide {
    width: 30%;
    display: flex;
	flex-direction: column;
}

.leftSide span {
    font-size: 2.5em;
    color: white;
}

.rightSide{
    display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	align-content: center;
}

.rightSide img {
    width: 70%;
}

.elipseDown{
    position: absolute;
    right: 0px;
    bottom: 0px;
    width:12%;
}

.accountControls{
    padding: 20px;
    width: 35%;
    margin-top: 100px;
    border: 3px solid #6000FC;
    border-collapse: separate;
    border-spacing: 15px;
    display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	align-content: center;
}

.accountControls span {
    font-size: 2em;
    color: white;
}
.accountControls span img{
    width: 15%;
    margin-right: 10px;

}

.accountControls Button{
    border-radius: 0 !important;
    margin-top: 25px;
    background-color: #6000FC;
    font-size: 2em;
    width: 220px;
}


.bodyDiv {
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #070a0d, #121921, #0f0613);
    background-size: cover;
    font-family: 'Raleway', sans-serif;
}

.logo{
    float:left;
    width:70%;
    margin-left: 15%;
    margin-top: 10%;
}

.dashboardContent{
    height: 96vh;
	display: flex;
	justify-content: space-between;
	align-items: stretch;
}

.dashboardLeft {
    padding-top: 2%;
    width: 15%;
    margin-left: 1%;
    display: flex;
	flex-direction: column;
}

.popularUsers {
    background-color: rgba(200, 200, 200, 0.05) !important;
    margin-top: 50px;
    font-size: 1.7em;
    border: 3px solid #6000FC;
    border-collapse: separate;
    border-radius: 7px;
}

.list-group-item {
    color: #00F0FF;
}

.profileName{
    color: #00F0FF;
    font-size: 1.2em;
}

.leftSide span {
    font-size: 2.5em;
    color: white;
}

.dashboardRight {
    width: 81%;
    margin-right: 1%;
}

.dashboardRight .head {
    width: 100%;
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: stretch;
	align-content: stretch;
}

.dashboardRight .head div{
    font-size: 1.5em;
    margin-right: 2%;
    margin-top: 0.5%;
    display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
	align-content: center;
}

.dashboardRight .head div a{
    margin-right: 50px;
}

.dashboardRight .head span{
    margin-left: 5%;
    margin-top: 1%;
    color: #00F0FF;
    font-size: 2em;
}

.profileIcon {
    width: 6%;
    margin-right: 2%;
    margin-bottom: 1.5%;
}

.buscador {
    display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: stretch;
	align-content: stretch;

    border-top: 3px solid #6000FC;
    border-bottom: 3px solid #6000FC;
    border-collapse: separate;
    border-spacing: 15px;

    margin-top: 0.5%;
    margin-bottom: 1%;
    margin-left: 2%;
    margin-right: 2%;
    width: 95%;
}

.buscador > span:first-of-type {
    margin-top: 5px;
    color: white;
    font-size: 1.4em;
}

.buscadorTop {
    margin-top: 1%;
    margin-bottom: 1%;
    margin-left: 4%;
    margin-right: 3%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.buscadorTop Button {
    width: 10% !important;
    font-size: 1.4em;
}

.buscadorBot {
    margin: 1%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

label {
   margin-right: 10px;
   margin-bottom: 0 !important;
}

.userField, .gameField{
    display: flex;
    flex-direction: row;
    align-items: center;
    color: white;
    font-size: 1.3em;
    width: 75%;
}
.selectOption *{
    background-color: rgba(200, 200, 200, 0.1) !important;
}

.form-select {
    height: 40px;
    width: 60%;
    border: 1px solid #6000FC !important;
    box-shadow: 0 0 2px #6000FC !important;
    background-color: rgba(200, 200, 200, 0.1) !important;
    color: white !important;
}

.form-select:focus {
    border: 1px solid #00F0FF !important;
}

.gameField {
    width: 30%;
}

.gameField label{
    width: 200px;
}

.platformField, .styleField {
    color: white;
    font-size: 1.3em;
    width: 30%;
}

.form-control-plaintext {
    outline: none !important;
    border: 1px solid #6000FC !important;
    box-shadow: 0 0 2px #6000FC !important;
    color: white !important;
    background-color: rgba(200, 200, 200, 0.1) !important;
    font-size: 1em !important;
}

.form-control-plaintext:focus {
    border: 1px solid #00F0FF !important;
}

.tablaUsuarios {
    border: 1px solid white;
    background-color: rgba(200, 200, 200, 0.1);
    overflow:auto;
    height: 70vh;
    width: 95%;
    margin-left: 2%;
}

tr:hover {
    background-color: rgba(0, 240, 255, 0.075) !important;
    font-weight: 600;
}
.auth-container{
    background-color: #181a22 ;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

